import style from './style.css'
import how1 from '../../assets/home/how1.webp'
import how2 from '../../assets/home/how2.webp'
import how3 from '../../assets/home/how3.webp'
import how4 from '../../assets/home/how4.webp'

const HowItWorks = () => {

return (      
	<div class={style.howItWork}>
        <div class={style.howItWorkContainer}>
            <h3>How this Works</h3>

            <div class={style.howItWorkGrid}>
                <HowItWorkCard />
            </div>
        </div>
	</div>
);
}
export default HowItWorks;



const HowItWorkCard = () => {

    const data = [
        { value: '1', label: 'Complete the form to check available immigration programs.', image: how1, width:"59", height:"84" },
        { value: '2', label: 'Receive instant results - a detailed list of programs you likely qualify for.', image: how2, width:"81", height:"80" },
        { value: '3', label: 'Select the program you are interested in.', image: how3, width:"81", height:"80" },
        { value: '4', label: 'Get a free consultation about the next steps.', image: how4, width:"83", height:"80" }
    ];    

    
    return(
        <>
            {data.map((e,i) =>{
                return(
                    <div class={style.howItWorkCard} key={i}>
                    <img src={e.image} alt="img" width={e.width} height={e.height} />
                    <h4>{e.value}.</h4>
                    <p>{e.label}</p>
                    </div>
                )
            })}
          
        </>
    )
}