/* eslint-disable no-prototype-builtins */
import { useEffect, useState } from 'preact/hooks';
import Select from 'react-select'
import style from './style.css'
import { route } from 'preact-router';
const TopBanner = () => {
   const [values, setValues] = useState({
    age:'',
    education:'',
    workExperience:'',
    language:'French',
    haveRelative:'Yes'
   })
   const [errors,setErrors]=useState()
  const age = [
    { value: '18-25', label: '18-25' },
    { value: '31-45', label: '31-45' },
    { value: '46-64', label: '46-64' },
    { value: '65+', label: '65+' },
  ]

  const education = [
    { value: 'High School Diploma', label: 'High School Diploma' },
    {
      value: '1-year Program Diploma or Certificate',
      label: '1-year Program Diploma or Certificate',
    },
    {
      value: '2-year Program Diploma or Certificate',
      label: '2-year Program Diploma or Certificate',
    },
    {
      value: '3-year Program Diploma or Certificate',
      label: '3-year Program Diploma or Certificate',
    },
    { value: 'Bachelors Degree', label: 'Bachelors Degree' },
  ]

  const experience = [
    { value: '1 year', label: '1 year' },
    { value: '2 year', label: '2 year' },
    { value: '4 year', label: '4 year' },
    { value: '8 year', label: '8 year' },
    { value: '9+ years', label: '9+ years' },
  ]

  const relatives = [
    { value: 'Yes', label: 'Yes' },
    { value: 'No', label: 'No' },
  ]
const handleInputChange=(val,name)=>{
    setValues({...values,[name]:val.value})
    setErrors({[name]:''})
}
const handleOnBlur=(val,name)=>{
   if(values[name] ===''){
   setErrors({[name]:`${name} is required`})
   }
}
useEffect(()=>{
    if(Object.values(values).every(val => val !== '')){
        localStorage.setItem('result',JSON.stringify(values))
    }
},[values])
const handleCheckOption=()=>{
    if(Object.values(values).every(val => val !== '')){
    route('/results/')
    }
}
  return (
    <div class={style.topBanner}>
      <div class={style.topBannerContainer}>
        <div class={style.topBannerTitle}>
          <h3>
            Check if You Can Immigrate to <span>Canada</span>
          </h3>
          <p>
            Get a full list of eligible immigration programs based on your
            profile.
          </p>
        </div>

        <div class={style.formGrid}>
          <h4>
            Complete the Form for <span>Instant Results</span>
          </h4>
          <div class={style.formBox}>
            <ul>
              <li class={style.active}>
                <AgeIcon />
              </li>
              <li>
                <EducationIcon />
              </li>
              <li>
                <ExperienceIcon />
              </li>
              <li>
                <LanguageIcon />
              </li>
              <li>
                <RelativesIcon />
              </li>
            </ul>
            <form>
              <div class={style.formScroll}>
                <SelectInput
                  label="What is your age?"                 
                  options={age}
                  name='age'
                  errors={errors}
                  onChange={handleInputChange}
                  onBlur={handleOnBlur}
                  counter="2"
                />
                <SelectInput               
                  label="What is your level of education?"
                  options={education}
                  name='education'
                  errors={errors}
                  onChange={handleInputChange}
                  onBlur={handleOnBlur}
                  counter="3"
                />
                <SelectInput
                  label="Do you have work experience inside Canada?"
                  options={experience}
                  name='workExperience'
                  errors={errors}
                  onChange={handleInputChange}
                  onBlur={handleOnBlur}
                  counter="4"
                />
                <SelectInput
                  label="Do you have Canadian relatives?"
                  options={relatives}
                  name='language'
                  errors={errors}
                  onChange={handleInputChange}
                  onBlur={handleOnBlur}
                  counter="5"
                />
                <SelectInput
                  label="Do you have Canadian relatives?"
                  options={relatives}
                  name='haveRelative'
                  errors={errors}
                  onChange={handleInputChange}
                  onBlur={handleOnBlur}
                  counter="6"
                />
              </div>
              <div class={style.buttonGrid}>
                <button class={style.backBtn}>Back</button>
                <button class={style.submitBtn} onClick={handleCheckOption}>Check My Options</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  )
}
export default TopBanner

const SelectInput = ({ label, options,name,errors,onChange,onBlur,counter}) => {
console.log("errrroooo->",errors?.[name]?errors?.[name]:'helo')
  return (
    <div class={style.formGroup}>
      <label htmlFor={`react-select-${counter}-input`}>{label}</label>
      <Select inputProps={{ id: name }}  options={options}  onChange={(e)=>onChange(e,name)} onBlur={(e)=>onBlur(e,name)} />
    </div>
  )
}

