import style from './style.css'
import calltoaction from '../../assets/home/calltoaction.webp'
import { Link } from 'preact-router/match';


const CallToAction = () => {

return (      
	<div class={style.callToAction}>
        <div class={style.callToActionContainer}>
            <div class={style.callToActionGrid}>
                <div class={style.callToActionCover}>
                    <img src={calltoaction} alt="img" width={393} height={296} />
                    <div className={style.typoAnimate}>
                        <div class="typewrite" data-period="2000" data-type='[ "Express Entry", "Provincial Nominee", "Atlantic Immigration", "Temporary Foreign Worker", "Federal Skilled Worker", "Student", "Working Holiday" ]'>
                        <span class="wrap" />
                        </div>
                    </div>
                </div>

                <div class={style.callToActionContent}>
                    <h2>Discover Your Options within <span>2 minutes!</span></h2>
                    <Link href="/">Go to Form</Link>
                </div>
            </div>
        </div>
	</div>
);
}
export default CallToAction;
