import style from './style.css'
import star from '../../assets/home/star.webp'
import authorstar from '../../assets/home/authorstar.webp'

const Testimonial = () => {

return (      
	<div class={style.testimonial}>
        <div class={style.testimonialContainer}>
            <h3>Our Clients</h3>

            <div class={style.testimonialGrid}>
                <TestimonialCard />
            </div>
        </div>
	</div>
);
}
export default Testimonial;



const TestimonialCard = () => {

    const data = [
        { author: 'Philip Wrigley', content: 'Would like to thank Ken and team for processing my visa for Canada would recommend UIS for all visa applications been hassle free.' },
        { author: 'Dani Jimenez Cremers', content: 'Mr. Kent has been incredibly helpful and always available when I needed him. Really attentive team and I would recommend them services. ' },
        { author: 'Ogunniyi T.', content: 'UIS Canada goes in depth and explains step by step procedure. They respond when you contact them and tell you everything you need to know if there are some doubts.'},
    ];    

    
    return(
        <>
            {data.map((e,i) =>{
                return(
                    <div class={style.testimonialCard} key={i}>
                    <div class={style.rating}>
                        <img src={star} alt="img" width={17} height={16} />
                        <img src={star} alt="img" width={17} height={16} />
                        <img src={star} alt="img" width={17} height={16} />
                        <img src={star} alt="img" width={17} height={16} />
                        <img src={star} alt="img" width={17} height={16} />
                    </div>
                    <p>{e.content}</p>
                    <h4><img src={authorstar} alt="img"  width={26} height={25} /> {e.author}</h4>
                    </div>
                )
            })}
          
        </>
    )
}